<template>
	<div class="container">
		<div class="card-panel" v-if="!disabled">
			<div class="row row-custom center" v-show="!user">
				<h2 class=""> Привет! </h2>
			</div>

			<div class="row center" v-if="user">
				<h2 class=""> Нажми на кнопку выбрать кому ты даришь подарок!</h2>
				<!--<h1 v-else> Выбор сделан </h1>-->
				<button class="btn btn-custom" id="show-modal" @click="choiceName()" :disabled="disabled"> Кнопка!
				</button>
			</div>

			<app-result v-if="showModal" @close="close">
				<h3 slot="header">Ты даришь подарок - </h3>
				<span slot="body">{{ select }}</span>
			</app-result>
		</div>

		<div class="card-panel" v-if="disabled">
			<h2 class="center"> Ты даришь подарок {{ select }}</h2>
		</div>
	</div>
</template>


<script>
	import firebase from 'firebase'
	import AppResult from '@/components/modal/AppResult'
	import db from '@/firebase/init'
	import {mapGetters} from 'vuex'

	export default {
		name: "AppIndex",
		components: {
			AppResult
		},
		data: () => ({
			showModal: false,
			select: null,
			id: null,
			disabled: null,

		}),

		created() {
			this.$store.dispatch('participants/SETCURRENTUSER')
			this.$store.dispatch('participants/SETPARTICIPANTS')
			this.disabled = localStorage.getItem('disabled');
			this.select = localStorage.getItem('select');
		},

		computed: {
			...mapGetters({
				user: 'participants/getUser',
				participants: 'participants/getParticipants'
			}),
		},

		methods: {
			close() {
				let LSdisabled = localStorage.getItem('disabled');
				if(!LSdisabled) {
					localStorage.setItem('disabled', true);
				}
				localStorage.setItem('select', this.select);
				this.disabled = true;
				this.showModal = false;
			},
			choiceName() {
				let rand = this.randomInteger();

				console.log(rand, 'rand 2');

				this.participants.forEach((el) => {

					if(el.id == rand && this.user.id == el.slug) {
						console.log('sovpalo');
						rand = this.randomInteger();
					}

					if(el.id == rand && !el.get) {
						this.id = el.slug;
						this.select = el.name;
					}
				})


				db.collection('users').doc(this.user.id).update({
					choice_user: this.select
				}).catch(err => {
					console.log(err)
				})

				db.collection('participants').doc(this.id).update({
					get: true
				}).catch(err => {
					console.log(err)
				})

				if(this.id) {
					this.showModal = true
				}

				// this.$router.push({ name: 'CurrentUser', params: {id: this.id}})

			},


			randomInteger() {
				let defaultArr = [],
					excludeArr = [],
					filteredArr = [];

				for( let i in this.participants ) {
					let el = this.participants[i];
					if(el.get) {
						console.log(el.id);
						excludeArr.push(el.id)
					}
				}

				for( let i = 0; i < this.participants.length; i++ ) {
					defaultArr.push(i)
				}

				for( var i = 0; i < defaultArr.length; i++ ) {
					if(excludeArr.indexOf(defaultArr[i]) == -1) {
						filteredArr.push(defaultArr[i]);
					}
				}

				console.log(excludeArr, 'excludeArr');
				console.log(filteredArr, 'filteredArr');

				let rand = filteredArr[Math.floor(Math.random() * filteredArr.length)];
				console.log(rand, 'rand 1');

				if(rand || rand === 0) {
					return rand
				} else {
					return this.randomInteger()
				}


			}
		}
	}
</script>

<style>

	.card-panel {
		max-width: 550px;
		margin: 60px auto 0;
	}

	.row-custom {
		margin-bottom: 44px;
	}

	.btn-custom {
		display: inline-block;
		position: relative;
		font-size: 22px;
		text-align: center;
		line-height: 44px;
		text-decoration: none;
		color: white;
		background-color: #6135b4;
		border-radius: 4px;
		min-width: 124px;
		margin: 50px auto 0;
		min-height: 44px;
		padding: 0 10px;
		top: -22px;
		text-shadow: 0 -1px -1px #5e35b1;
		box-shadow: 0 4px 0 #492e92, 0 5px 5px 1px rgba(0, 0, 0, 0.4);
		transition: all 0.15s ease-in-out;
	}

	.btn-custom:hover {
		background-color: #6635b9;
		text-shadow: 0 -1px -1px #5e35b1;
		box-shadow: 0 4px 0 #492e92, 0 5px 5px 1px rgba(0, 0, 0, 0.4);
	}

	.btn-custom:active {
		top: -18px;
		box-shadow: none;
	}

	.btn-custom:disabled {
		background-color: #51329b !important;
		color: #ffffff !important;
		box-shadow: 0 4px 0 #3a2e73, 0 5px 5px 1px rgba(0, 0, 0, 0.4);

		/*top: -18px;*/
		/*box-shadow: none;*/
	}
</style>


